.searchForm {
  width: 100%;
  max-width: 400px;
  display: flex;
  align-items: center;
  margin: 24px auto;
}

.searchInput {
  flex: 1;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.searchButton {
  width: 100%;
  background-color: #994636;
  color: #fff;
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.searchButton:hover {
  background-color: #994636;
}