.container {
  max-width: 800px;
  margin: 0 auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.movie-list-item {
  width: calc(25% - 12px);
  margin-bottom: 10px;
}

.moviePoster {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.movie-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  text-decoration: none;
  color: #333;
  transition: background-color 0.3s ease;
  text-align: center;
}

.movie-link img {
  width: 100%;
  border-radius: 8px 8px 0 0;
}

.movie-link:hover {
  background-color: #f5f5f5;
}
